import React, {ChangeEvent, DetailedHTMLProps, InputHTMLAttributes} from 'react'
import s from './SuperRange.module.css'

//    
type DefaultInputPropsType = DetailedHTMLProps<InputHTMLAttributes<HTMLInputElement>, HTMLInputElement>

//               
// (   value: string, onChange: ...;      DefaultInputPropsType)
type SuperRangePropsType = DefaultInputPropsType & { //  +       
  onChangeRange?: (value: number) => void
  value: number
  max: number
  min: number
  bgColor?: string
  styleClassNameRange?: React.CSSProperties | string // React.CSSProperties   (st)
  styleProgressOverlay?: React.CSSProperties         //    st
};

const SuperRange: React.FC<SuperRangePropsType> = (
  {
    type, //          
    onChange, onChangeRange,
    className,
    value, max, min,
    bgColor, styleClassNameRange, styleProgressOverlay,
    ...restProps//       restProps
  }
) => {
  const onChangeCallback = (e: ChangeEvent<HTMLInputElement>) => {
    onChange && onChange(e) //   

    onChangeRange && onChangeRange(+e.currentTarget.value)
  }

  const finalRangeClassName = `${s.range} ${className ? className : ''}`

  const pointPosition = value * 100 / max
  //---  progress  
  const progressBGColor = bgColor ? bgColor : '#6d5dfc'


  const selfPointPosition1 = (value * 26) / 100


  const finalRange = styleClassNameRange ? `${s.range} ${styleClassNameRange}` : s.range

  return (
    <div className={finalRange}>
            <span className={s.valueCoords}
                  style={{left: `calc(${pointPosition}% - ${selfPointPosition1}px)`}}>{value}</span>
      <div className={s.progressOverlay} style={styleProgressOverlay}>
        <div className={s.progress} style={{
          zIndex: 'var(--zIndexProgress, ModalEditedForHeader)',
          background: `var(--bgProgress, ${progressBGColor})`,
          width: `${pointPosition}%`,
        }}/>
      </div>
        <div className={s.input}>
            <input
                step={1}
                max={max}
                value={value}
                type={'range'}
                onChange={onChangeCallback}
                className={finalRangeClassName}
                {...restProps} //        (value   )
            />
            <div className={s.innerRound}></div>
        </div>
    </div>
  )
}

export default SuperRange