import React, {ChangeEvent, DetailedHTMLProps, InputHTMLAttributes, KeyboardEvent,} from 'react'
import s from './SuperInputText.module.css'
import passViewOn from "../../../../assets/img/view.svg";
import passViewOff from "../../../../assets/img/no-view.svg";

//    
type DefaultInputPropsType = DetailedHTMLProps<InputHTMLAttributes<HTMLInputElement>, HTMLInputElement>

//               
// (   value: string, onChange: ...;      DefaultInputPropsType)
type SuperInputTextPropsType = DefaultInputPropsType & { //  +       
    onChangeText?: (value: string) => void
    onEnter?: () => void
    error?: string
    spanClassName?: string;
    passOn?: boolean;
    setPassOn?: (value: boolean) => void;
    // onChange?: Dispatch<SetStateAction<string>>;
}

const SuperInputText = React.forwardRef<HTMLInputElement, SuperInputTextPropsType>((
    {
        type, //          
        onChange, onChangeText,
        onKeyPress, onEnter,
        error,
        className, spanClassName,
        passOn,
        setPassOn,
        ...restProps//       restProps
    }, ref
) => {
    const onChangeCallback = (e: ChangeEvent<HTMLInputElement>) => {
        onChange //    onChange
        && onChange(e) //     ( onChange  )

        onChangeText && onChangeText(e.currentTarget.value)
    }
    const onKeyPressCallback = (e: KeyboardEvent<HTMLInputElement>) => {
        onKeyPress && onKeyPress(e);

        onEnter //    onEnter
        && e.key === 'Enter' //     Enter
        && onEnter() //   
    }

    const changeView = () => {
        setPassOn && setPassOn(!passOn)
    }

    const finalInputClassName = `${s.input} ${error ? s.errorInput : s.superInput} ${className}`

    return (
        <div className={s.inputWrapper}>
            <input
                type={type}
                onChange={onChangeCallback}
                onKeyPress={onKeyPressCallback}
                className={finalInputClassName}
                ref={ref}

                {...restProps}
            />

            {type === ('password') &&
                <img className={s.passwordControl} src={passOn ? passViewOn : passViewOff} alt="passwordOn/Off"
                     onClick={changeView}/>}
            {type === ('text') &&
                <img className={s.passwordControl} src={passOn ? passViewOn : passViewOff} alt="passwordOn/Off"
                     onClick={changeView}/>}

        </div>
    )
})

export default SuperInputText
